.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;

  @include onTablet () {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 24px;
  }

  @include onDesktop () {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;
  }

  &--off {
    display: block;

    @include onTablet () {
      display: grid;
    }
  }
}
